export default {
  name: 'Pager',
  props: {
    totalPage: {
      type: Number,
      required: true
    },
    currentPage: {
      type: Number,
      required: true
    },
    hideIfOnePage: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    pages () {
      let pages = [
        1,
        this.totalPage,
        this.currentPage,
        this.currentPage - 1,
        this.currentPage - 2,
        this.currentPage + 1,
        this.currentPage + 2
      ]
      let u = unique(pages.filter((n) => n >= 1 && n <= this.totalPage).sort((a, b) => a - b))
      let u2 = u.reduce((prev, current, index, array) => {
        prev.push(current)
        if (
          array[index + 1] !== undefined &&
          array[index + 1] - array[index] > 1
        ) {
          prev.push('···')
        }
        return prev
      }, [])
      return u2
    }
  },
  methods: {
    onClickPage (n) {
      if (n >= 1 && n <= this.totalPage) {
        this.$emit('update:currentPage', n)
      }
    }
  }
}

function unique (array) {
  // return [...new Set(array)]
  const object = []
  array.map(number => {
    object[number] = true
  })
  return Object.keys(object).map(s => parseInt(s, 10))
}
